.share_btn--container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}
  
.btn__create-card {
  width: 199px;
  height: 50px;
  background-color: #e17334;
  -webkit-box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
  border-radius: 12px;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  border: 0;
  color: #ffffff;
  font-size: 18px;
  margin: 30px 0;
  cursor: pointer;
  outline: none;
}
  
.btn__create-card--done {
  background-color: #d5d5d5;
}
  
.share__icon {
  margin-right: 10px;
  font-size: 22px;
}
  
.twitter-share-button {
  background-color: #00a0f5;
  color: white;
  height: 30px;
  border-radius: 8px;
  margin-top: 20px;
  display: block;
  width: 181px;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
  
.share__title {
  font-size: 18px;
  color: #54585a;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  padding-top: 20px;
}
  
.share__twitter {
  width: 390px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-top: 1px solid #d8d8d8;
}
  
.twitter__hidden {
  visibility: hidden;
  height: 0;
}
  
.twitter__card--url {
  text-align: center;
}
  
.twitter__text {
  padding: 5px;
}
  