.fill__label {
  color: #114e4e;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 10px;
}
  
.fill__title--form {
  margin-bottom: 20px;
}
  
.fill__full-name {
  margin-top: 25px;
}
  
.block {
  display: block;
  height: 30px;
  padding-left: 9px;
  width: 281px;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #c5c5c5;
  margin-top: 6px;
}
  
.fill__add-img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
  
.square__img {
  width: 30px;
  height: 30px;
  border: 1px solid #a2deaf;
  margin-left: 20px;
  margin-top: 6px;
  background: top center no-repeat;
  background-size: cover;
}
  
#btn__add-img {
  display: none;
}
  
.btn__img--false {
  background-color: #e17334;
  border: none;
  margin: 6px;
  width: 140px;
  height: 30px;
  border-radius: 3px;
  color: #f5f5f5;
  font-family: "Open Sans", sans-serif;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
  outline: none;
  cursor: pointer;
}

.form__skills {
  text-transform: uppercase;
  color: #114e4e;
  margin-bottom: 10px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}
  