.home__container {
  text-align: center;
  background-color: #f5f5f5;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}
  
.home__logo {
  margin-top: 94px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 250px;
  height: 64px;
  background-size: cover;
}
  
.home__main {
  margin-top: 35px;
}
  
.home__main--title {
  color: #e17334;
  font-size: 19px;
  font-family: "Merriweather", serif;
  margin: 0;
  padding-bottom: 4px;
}
  
.home__main--text {
  color: #114e4e;
  font-size: 13px;
  font-family: "Open Sans", sans-serif;
  margin: 0;
}
  
.home__options {
  padding: 14px 20px;
  list-style: none;
  border: 1px solid rgba(225, 115, 52, 0.21);
  border-radius: 4px;
  display: grid;
  grid-gap: 24px;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 268px;
  margin: 14px auto 39px;
}
  
.home__options--text {
  color: #54585a;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
}
  
.home__link {
  background-color: #438792;
  color: white;
  width: 129px;
  height: 42px;
  border-radius: 21px;
  font-weight: 600;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-bottom: 30px;
}
  
.home__options--icon {
  color: #e17334;
  font-size: 28px;
}
  
  @media all and (min-width:768px){
    
  .home__container {
    position: relative;
  }

  .home__logo {
    width: 419px;
    height: 107px;
  }

  .home__main {
    margin-top: 31px;
  }

  .home__main--title {
    font-size: 24px;
  }

  .home__main--text {
    font-size: 16px;
    width: 331px;
    margin: 0 auto;
  }

  .home__options {
    width: 325px;
    padding: 15px 20px;
    margin-bottom: 186px;
  }

  .home__link {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    bottom: 59px;
  }
}